<template>
    <div>
        <p>{{ListItem.sign}}</p>
        <img :src="imgSrc" alt=""/>
        <ul class="ListAdd">
            <li><label for="remark"> 款项：</label><input type="text" id="remark" v-model="ListItem.remark"></li>
            <li class="money">
                <label for="cost"> 金额：</label><input type="number" id="cost" @keyup.enter="test(ListItem.cost)" @focus="show()" v-model="ListItem.cost">
                <span v-show="flag">不能为空</span>
            </li>
            <li  class="btn">
                    <button @click="listAdd">确认</button>
                    <button @click="listDel">返回</button>
            </li>
        </ul>
        <foot-nav></foot-nav>
    </div>
</template>

<script>
import client from '../assets/clientStorage.js'
import footNav from './footnav.vue'
export default {
  name:'List',
  components:{footNav},
  data(){
      return{
        flag:false,
        ListItem:{remark:'',cost:'',sign:''},
        sum:[],
        imgSrc:""

      };
  },
created:function(){
    this.ListItem.sign=client.fetch("sign").sort;
    //console.log(this.ListItem.sign);
    this.imgSrc=client.fetch("sign").imgSrc;   
    this.sum=client.fetch('items');
},

  methods:{

        listAdd(ListItem){
           this.sum=client.fetch('items');
            if(!this.ListItem.cost){
                 this.flag=true;
                return;
            }
            
            this.sum.push(this.ListItem);
            console.log(this.sum);
            client.save(this.sum,"items");
           const router=this.$router;

        this.listDel();
        },
        listDel(){
            this.ListItem.remark='',
            this.ListItem.cost=''  ,
             this.$router.push({path:'/Category'});
        },
        test(data){
            this.listAdd(data);
        },
        show(flag){
                this.flag=false;
            
        }
      },
      
    
}
</script>
<style>
    .ListAdd{
        list-style:none; 
        display: flex;
        /* width:600PX; */
        align-items:center;
        flex-flow:column nowrap;
        justify-content: center;

    }
    .ListAdd li{
        margin:10px 0;
    }    
 
    .btn button{
        margin:0 10px;

    } 
    .money{
        position: relative;
    }
    .money span{
        position: absolute;
        right: 10px;top:0px;
        color:red;
    
    }    
</style>






