<template>
  <div>
   <p>添加类型</p> 
   <label for="add">类型</label>
    <input type="text" id="add" @keyup.enter="add(addItem)" @focus="show()" v-model="addItem">
    <span class="tip" v-show="flag">不能为空</span>
    <br>
    <button @click="add(addItem)">确定</button>
    <button @click="back()">取消</button>

   
  </div>
</template>

<script>
import client from '../assets/clientStorage.js'

export default {
  data(){
    return{
      addItems:[],
      addItem:"",
      flag:false
    }
  },
  methods:{
    add(addItem){
      var datas=client.fetch("datas");
      var item={sort:addItem,imgSrc:'http://localhost:8000/assets/image/star.png'};
     
     
      // this.addItems=client.fetch('addItem');
      // client.save(this.addItems,'addItems');
      
      if(!this.addItem){
        this.flag=true;
        return;
      }
       datas.push(item);

       client.save(datas,'datas');
      // this.addItems.push(this.addItem);
    this.back();
    },
    back(){
     
      this.$router.push({path:'/Category'});
    },
    show(flag){
                this.flag=false;
            
    }
  }
}
</script>

<style>
  div{
    position: relative;
  }
 .tip{
    color: red;
    /* font-size:0.3rem; */
    position:absolute;
    left:65%;
    top:35%;
  }
  button{
      margin:0 20px;

  } 
</style>




