<template>
    <div>
        <p>花费明细</p> 
        <span>类别</span>
        <span>￥</span>
        <span>备注</span>   
        <ul class='big_menu' v-for='(item,index) in items'>
            <li>
                <span v-text='item.sign'></span>
            </li>
            <li>
                <input v-model='item.cost' readonly='true'>元</input>
            </li>
            <li>
                <input v-model='item.remark' readonly='true'></input>
                <div v-if="!item.isShow">
                   <img src="http://localhost:8000/assets/image/edit.png" alt="" @click="changeListItem(index)">
                    <img src="http://localhost:8000/assets/image/delete.png" alt="" @click="delListItem(index)">
                </div>
                <div v-else>
                    <img src="http://localhost:8000/assets/image/true.png" alt="" @click="overListItem(index)">
                    <img src="http://localhost:8000/assets/image/false.png" alt="" @click="cancelListItem(index)">
                </div>
                
            </li>
           
        </ul>
        <p>总消费：{{total}}</p>
        <foot-nav></foot-nav>
    </div>
    
  
</template>

<script> 
import client from '../assets/clientStorage.js'
import footNav from './footnav.vue'
export default {
  components:{footNav},
  data(){
      return{
          items:[],
          total:0
        //   isShow:false
      }
  },
  created:function(){
        this.items=client.fetch('items');
        this.calcSum();
      
    //   console.log(this.items);
  },
  computed:{
      uls:function(){
          return document.querySelectorAll('ul');
      }
  },
  methods:{
      calcSum:function(){
        let that=this;
        this.total=0;
        this.items.forEach(function(v,i){
          v.isShow=false;
          console.log(parseInt(v.cost));
          that.total+=parseInt(v.cost);

      });
      },
      changeListItem:function(index){
//将所有ul里面的input变为不可用
        // for(var j=0;j<this.uls.length;j++){
        //      for(var i=0;i<this.uls[j].querySelectorAll('input').length;i++){
        //         this.uls[j].querySelectorAll('input')[i].setAttribute('readonly',"true")
        //     }
        // }dex
        this.items[index].isShow=true;
            console.log(this.uls[index].className+=' active');
            for(var i=0;i<this.uls[index].querySelectorAll('input').length;i++){
                this.uls[index].querySelectorAll('input')[i].removeAttribute('readonly')
            }
      },
      delListItem:function(index){
        this.items.splice(index,1);
        client.save(this.items,'items');
         this.items=client.fetch('items');
        this.calcSum();
      },
      overListItem:function(index){
        this.items[index].isShow=false;
        console.log(document.querySelectorAll('ul')[index].setAttribute('class','big_menu'));
        for(var i=0;i<this.uls[index].querySelectorAll('input').length;i++){
            this.uls[index].querySelectorAll('input')[i].setAttribute('readonly',"true")
        }
        client.save(this.items,'items');
       this.items=client.fetch('items');
        this.calcSum();

        
      },
      cancelListItem:function(index){
       this.items[index].isShow=false;
        for(var i=0;i<this.uls[index].querySelectorAll('input').length;i++){
            this.uls[index].querySelectorAll('input')[i].setAttribute('readonly',"true")
        }
        console.log(document.querySelectorAll('ul')[index].setAttribute('class','big_menu'));
        
      }

  }
}
</script>
<style>
 
    p{
        align-items: center;
    }
    div>span{
       margin-right:70px;
    }
    .big_menu{
        list-style: none;
        justify-content: space-between;
        position: relative;
        display: flex;
    }
    .big_menu>li{
        display: inline-block;
        padding: 0px;
        
    }
  .big_menu input{
        text-align: right;
        width: 130px;
        border: none;
    }
    .big_menu.active input{
        border:1px solid black;
    }
    /* .big_menu>li:nth-child(2){
        align-items: flex-start;
    } */
    .big_menu>li div{
        display: inline-block;
         margin-left:40px;
    }
    .big_menu>li div .btn_d{
        border: none;
        margin-left: -20px;
        font-size: 0.4rem;
    }
    .big_menu img{
        width:40px;
        height:40px;
       
    }
</style>




